import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/apicall.service';
import { GlobalService } from 'src/app/services/global.service';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-model',
  templateUrl: './model.page.html',
  styleUrls: ['./model.page.scss'],
})
export class ModelPage implements OnInit {
  @Input() Category :any;
  @Input() updateMRLogin :any;
  @Input() mtp_id :any;
  @Input() dcr_id :any;
  Togglevaluee: any  = 'deactivate';
  public usersignupdata:any= {username:'', password:'' , role:'mr', status:'activate'}
  public mrloginupdate:any= {u_id:'', username:'', password:''}
  public allMRlogin : any ;
  public allproducts : any;
  public alldcrproducts : any;
  constructor(private router:Router, public apicall : ApicallService, public global : GlobalService, public toast : ToastService, private modalController: ModalController) { }

async ngOnInit() {
    console.log(this.Category);
    if(this.Category == 'seeproduct') {
      console.log(this.mtp_id);
      await this.apicall.api_seeproductbymtp(this.mtp_id);
     await this.global.SeeProductbymr.subscribe(res=>{
             this.allproducts = res;
             console.log(this.allproducts);
      });
    }
    else if(this.Category == 'seedcrproduct') {
      console.log(this.dcr_id);
      await this.apicall.api_seeproductbydcr(this.dcr_id);
      await this.global.SeeProductbydcr.subscribe(res=>{
              this.alldcrproducts = res;
              console.log(this.alldcrproducts);
       });
    }
    else {
      console.log('no Category')
    }
    console.log(this.updateMRLogin);
    this.mrloginupdate = this.updateMRLogin;
  }

  signup(){
    console.log(this.usersignupdata);
    // this.usersignupdata.status = this.Togglevaluee;
    this.apicall.api_addMrsignupCredentials(this.usersignupdata);
    this.usersignupdata = { username:'', password:'', role:'mr', status:'activate' };
    this.close();
  }
  close() {
    this.modalController.dismiss(close).then(r => {
      console.log(r);
    });
  }

  async changeToggle($event: CustomEvent) {
    console.log($event.detail.checked);
    console.log($event.detail.value);
    if ($event.detail.checked == true) {
          this.Togglevaluee = 'activate'
          console.log(this.usersignupdata.status);
    }
    else{
      this.Togglevaluee = 'deactivate'
      console.log(this.usersignupdata.status);
    }
  }

 async updateMrLogin(){
    console.log(this.mrloginupdate);
   await this.apicall.api_updateMRlogin(this.mrloginupdate);
   this.getallmrData();
   this.close();
  }

  async getallmrData(){
    await this.apicall.api_getMRloginData();
     this.global.AllmrLoginData.subscribe(res =>{
       this.allMRlogin = res;
     })
   }

}
